@import "../helpers/variables";

main section.title-screen {
  z-index: 1;
  height: 100vh;
  width: 100%;
  // background: $secondary-background;
  background: rgb(0, 0, 0);
  // background: linear-gradient(
  //   153deg,
  //   rgba(0, 0, 0, 1) 0%,
  //   rgba(104, 70, 16, 1) 25%,
  //   rgba(89, 60, 14, 1) 29%,
  //   rgba(0, 0, 0, 1) 39%,
  //   rgba(104, 70, 16, 1) 51%,
  //   rgba(104, 70, 28, 1) 53%,
  //   rgba(0, 0, 0, 1) 68%
  // );
  // background: rgb(0, 0, 0);
  // background: linear-gradient(
  //   153deg,
  //   rgba(0, 0, 0, 1) 0%,
  //   rgba(104, 70, 16, 1) 37%,
  //   rgba(0, 0, 0, 1) 83%
  // );
  background: rgb(0, 0, 0);
  background: linear-gradient(
    153deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(104, 70, 16, 1) 37%,
    rgba(0, 0, 0, 1) 73%
  );
  position: relative;
  overflow: hidden;

  &::before {
    content: "";
    height: 110vh;
    aspect-ratio: 1/1;
    border-radius: 500px;
    background: #020202;
    display: block;
    position: absolute;
    transform: translate(-450px);
    box-shadow: 10px 10px 40px $space-dust-color,
      inset 0 0 10px $space-dust-color;
    z-index: 1;
  }

  // &::after {
  //   content: "";
  //   position: absolute;
  //   width: 110vw;
  //   height: 5em;
  //   background: $space-dust-color;
  //   display: block;
  //   transform: rotate(343deg) translate(0, -200px);
  //   filter: blur(40px);
  //   overflow-x: hidden;
  // }

  article {
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  div#particles {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    // z-index: 1;
  }
}
